        {
          title: "勤務体系名",
          slot: "Num2",
          className: 'bluetbl',
          key: "Name"
        },
        {
          title: "勤務形態",
          slot: "Num3",
          className: 'bluetbl',
          key: "Form"
        },
        {
          title: "所定時刻",
          slot: "Num4",
          className: 'bluetbl',
          key: "time1"
        },
        {
          title: "所定時間",
          width: 100,
          slot: "Num5",
          className: 'bluetbl',
          key: "time2"
        },
        {
          title: "休憩時間1",
          slot: "Num6",
          className: 'bluetbl',
          key: "relax1"
        },
        {
          title: "休憩時間2",
          slot: "Num7",
          className: 'bluetbl',
          key: "relax2"
        },
        {
          title: "休憩時間3",
          slot: "Num8",
          className: 'bluetbl',
          key: "relax3"
        },
        {
          title: " ",
          slot: "action",
          width: 220,
          className: 'bluetbl',
          align: "center"
        }
      ],
      data6: [
        {
          Num: "0101",
          Name: "朝8時固定",
          Form: "固定勤",
          time1: "8:00～17:00",
          time2: "8:00",
          relax1: "7:00～ 8:00",
          relax2: "12:00～13:00",
          relax3: ""
        },
        {
          Num: "0102",
          Name: "朝9時固定",
          Form: "固定勤",
          time1: "9:00～18:00",
          time2: "8:00",
          relax1: "8:00～ 9:00",
          relax2: "12:00～13:00",
          relax3: ""
        },{
          Num: "0201",
          Name: "朝9時固定",
          Form: "固定勤",
          time1: "9:00～18:00",
          time2: "8:00",
          relax1: "8:00～ 9:00",
          relax2: "12:00～13:00",
          relax3: ""
        },{
          Num: "0301",
          Name: "8時間制",
          Form: "フリー",
          time1: "",
          time2: "8:00",
          relax1: "出勤 4:00後～60分",
          relax2: "出勤 8:00後～30分",
          relax3: ""
        },{
          Num: "0401",
          Name: "10時コア",
          Form: "フレックス",
          time1: "10:00～16:00",
          time2: "8:00",
          relax1: "12:00～13:00",
          relax2: "",
          relax3: ""
        }
      ],
      editIndex:-1,
      num:'',
    };
  },
  computed: {
    rotateIcon() {
      return ["menu-icon", this.isCollapsed ? "rotate-icon" : ""];
    },
    menuitemClasses() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    handleSubmit (name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
                this.$Message.success('Success!');
            } else {
                this.$Message.error('Fail!');
            }
        })
    },
    handleReset (name) {
        this.$refs[name].resetFields();
    },
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
    show(index) {
      this.$Modal.info({
        title: "下記の勤務体系を廃止してもよらしいですか？",
        content: 
        `勤務体系コー：${this.data6[index].Num}
        <br>
        勤務体系名：${this.data6[index].Name}
        <br>
        勤務形態：${this.data6[index].Form}`
      });
    },
    remove(index) {
      this.data6.splice(index, 1);
    },
    edit(index){
      this.editIndex = index
    },
    next() {
      if (this.currentStep === 3) {
        return
      } else {
        this.currentStep += 1
      }
    },

    back() {
      if (this.currentStep === 0) {
        return
      } else {
        return (this.currentStep -= 1)
      }
    }
  }
};
</script>




